<template>
  <div class="login">
    <header>
      <div class="register" @click="changepage({name: 'register'})">注册</div>
    </header>
    <div class="loginbox">
      <div class="title">快捷登录</div>
      <div class="form-box">
        <div class="input phoneinp">
          <input class="inptext" type="text" placeholder="请输入手机号码" v-model="user.phone" />
        </div>
        <div class="codeinp clearfix">
          <div class="fl input code-input">
            <input class="inptext" type="text" placeholder="验证码" v-model="user.code" />
          </div>
          <div class="fl code-btn" @click="getCode">{{codeContent.txt}}</div>
        </div>
        <div class="login-btn" @click="login">登录</div>
        <div class="subtitle" @click="changepage({name: 'pwdlogin'})">密码登录</div>
      </div>
      <div class="third-part">
        <div class="subtitle"> ——————— 第三方登录 ———————</div>
        <div class="icon clearfix">
          <span>
            <i class="wechat"></i>
          </span>
          <span>
            <i class="weibo"></i>
          </span>
          <span>
            <i class="qq"></i>
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// 同理后台登录 在客户端注册时生成 生成登录验证token 并在register.vue注册成功时保存token
  import{valid} from '../assets/js/valid'  // 导入valid.js

  export default {
    name:'login',

    data(){
      return{
        // 确保this.axios({})关联成功，定义对象 —— 绑定在html上 _ ajax中传入params
        user:{
          phone:'', //手机号19849042805
          code: ''
        },

        codeContent: {
          txt: '获取验证码',
          time: 5
        }
      }
    },

    methods:{
      // 获取验证码
      getCode(){
        // 手机成功收到短信关闭发短信功能 —— 导入 证验手机号 (创建assets文件夹并在js文件夹下创建valid.js)
        //验证手机号
        if(!valid.isPhone(this.user.phone)){
          // 加载
          this.$notify({
            type: 'warning',
            message: '手机号码格式不正确'
          });
          return;
        }

        this.codeContent.txt = this.codeContent.time + 's后重新获取';

        let timer = setInterval(() => {

          this.codeContent.time --;
          if(this.codeContent.time == -1){
            clearInterval(timer);
            timer = null;
            this.codeContent.txt = '获取验证码';
            this.codeContent.time = 5;
          }else{
            this.codeContent.txt = this.codeContent.time + 's后重新获取';
          }
        },1000) //定时每个一秒变化一次 —— 后台创建用户表user

        // console.log('this.user.phone=>',this.user.phone);
        this.axios({
          method:'GET',
          // url:'http://127.0.0.1:20008/getMessageCode', //拼接后台路由控制路径
          url: '/getMessageCode', //路径在main.js中
          params:{
            phone: this.user.phone
          }
        }).then(result =>{
          // console.log('getCode result=>',result);
          // 判读存贮cookie
          if(result.data.status == 20000){
            //存储cookie   / this.$cookies.set(键名, 值, 过期时间)
            this.$cookies.set(result.data.__ctk.key, result.data.__ctk.ctk, result.data.time);

          }
        }).catch(err =>{
          // console.log('客户端登录出错！');
        })
      },

      //登录
      login(){
        this.$toast({
          duration: 0,
          message: '登录中...'
        });

        this.axios({
          method: 'POST',
          url: '/appLogin', //路径设置放在main.js中
          
          //post请求参数
          // 登录方式loginType ==> 0: 手机号+验证码登录，1：手机号+密码登录
          data: {
            phone: this.user.phone,
            key: 'code', //
            code: this.user.code,
            loginType:0
          }
        }).then(result =>{
          // console.log('login result =>',result); 

          this.$toast.clear(); //清除加载提示

          // 登录验证token
          if (result.data.status == 2000) {
            this.$cookies.set(result.data.__utk.key, result.data.__utk.utk, result.data.time * 24 * 60 * 60);

          }    

          //登录成功提示
          this.$notify({
            type: result.data.status == 2000 ? 'success' : 'warning',
            message: result.data.msg,

            //当前关闭提示后跳转
            onClose: () => {
              if (result.data.status == 2000) {
                this.$router.push({name: 'home'});
              }
            }
          });

        }).catch( err=>{
          // console.log('登录出错');
          this.$toast.clear();
        })

      },

      // changepage
      changepage(o){
        this.$router.push(o);
      }
    }
  }
</script>

